import wallet from "@/common/js/wallet.js";
export default {
	data() {
		return {
			mnemonic: []
		}
	},
	onLoad() {
		this.mnemonic = this.$Wallet.getCurrentWallet().mnemonic.split(' ');
	},
	methods: {
		next(){
			uni.navigateTo({
				url: 'backupMnemonic'
			})
		}
	}
}
